:root {
  --basic-blue: #61dafb;
  --basic-grey: #282c34;
  --basic-red: rgb(194, 55, 55);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.App {
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background-color: var(--basic-grey);
}

.counter {
  border: 3px solid var(--basic-blue);
  border-radius: 10px;
  display: grid;
  grid-template-rows: 180px 1fr;
  grid-template-columns: 1fr 1fr;
  width: 350px;
  height: 300px;
  padding: 10px;
}

.counter__screen {
  background-color: var(--basic-blue);
  border-radius: 10px;
  margin-bottom: 10px;
  grid-column: 1 / 3;
  display: grid;
  justify-content: center;
  align-items: center;
  font-size: 60px;
  font-weight: bold;
  color: var(--basic-grey);;
}

.counter__buttons {
  grid-row: 2 / 3;
  grid-column: 1 / 3;
  border: 3px solid var(--basic-blue);
  border-radius: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  padding: 10px;
  gap: 10px;
}

.error {
  color: var(--basic-red);
}

.button {
  display: grid;
  justify-content: center;
  align-items: center;
  background-color: var(--basic-blue);
  min-height: 60px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 30px;
  font-weight: bold;
  border: none;
}

.button:disabled {
  opacity: 0.5;
}